<template>
  <div class="login-box">login
    <h2>회원가입</h2>
    <form id="appForm" @submit="checkForm">
      <div class="user-box">
        <input v-model="memberId" id="memberId" type="text" required="" />
        <label>아이디</label>
      </div>
      <div class="user-box">
        <input v-model="memberPasswd" id="memberPasswd" type="password" required="" />
        <label>비밀번호</label>
      </div>
      <div class="user-box">
        <input v-model="memberName" id="memberName" type="text" required="" />
        <label>성명</label>
      </div>

      <div class="user-box">
        <input v-model="memberEamil" id="memberEamil" type="email" required="" />
        <label>이메일 주소</label>
      </div>
      <div class="btn_login">
        <a @click="clickEvent">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        회원가입
      </a>
      </div>
    </form>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  data() {
    return {
      memberId: '',
      memberPasswd: '',
      memberName:'',
      memberEamil:'',

    };
  },
  methods: {
    clickEvent: function () {
      if (this.memberId === '') {
        alert('ID를 입력하세요');
        return;
      } else if (this.memberPasswd === '') {
        alert('비밀번호를 입력하세요');
        return;
      } else if (this.memberName === '') {
        alert('성명을 입력하세요');
        return;
      } else if (this.memberEamil === '') {
        alert('이메일 주소를 입력하세요');
        return;    
      }  
      axios
        .post('/main/register', {
          memberId: this.memberId,
          memberPasswd: this.memberPasswd,
          memberName: this.memberName,
          memberEamil: this.memberEamil,
        })
    },
  },
};
</script>
